<template>
    <div class="appbox">
        <div class="inputBox"><input type="text" v-model="nickname" placeholder="请输入名称"></div>
        <button @click="setNickname">确认</button>
    </div>
</template>

<script>
    import { setUserInfo } from "@/api/user"
    import Toast from 'vant/lib/toast'
    import 'vant/lib/toast/style'
    import { mapGetters } from 'vuex'

    export default {
        name: "Nickname",
        computed: {
            ...mapGetters([
                'avatar',
                'name',
                'token'
            ])
        },
        data()
        {
            return {
              nickname:''
            }
        },
        mounted()
        {
            this.nickname = this.name
        },
        methods:{

            setNickname()
            {
                if (this.nickname == '' || this.nickname.length == 0)
                {
                    Toast('请输入用户名')
                    return
                }
                setUserInfo({nickname:this.nickname}).then(res => {

                    Toast('设置成功')
                    this.$store.dispatch('user/setName', this.nickname).then(() => {

                        this.$router.go(-1)
                    }).catch(() => {

                    })
                })
            }
        }
    }
</script>

<style scoped>
    .appbox {
        background: #f5f5f5;
        height: 100vh;
        text-align: center;
    }
    .appbox .inputBox {
        padding: 0.15rem;
        overflow: hidden;
    }
    .appbox .inputBox input {
        background: #fff;
        width: 3.35rem;
        height: 0.45rem;
        padding-left: 0.1rem;
        border-radius: 0;
        font-size: 0.14rem;
        color: #3F3F3F;
    }
    .appbox button {
        width: 3.45rem;
        height: 0.45rem;
        background: #E33C3D;
        font-size: 0.17rem;
        color: #F5F5F5;
    }
</style>



